class Node {
  isEnter = false;
  isExit = false;
  isWallOrVisited = false;
  x;
  y;
  predecessor = undefined;
  constructor(y, x) {
    this.x = x;
    this.y = y;
  }
}

function alg(nodes, startY, startX) {
  let queue = [];
  let visited = [];
  queue.push(nodes[startY][startX]);
  nodes[startY][startX].isWallOrVisited = true;
  while (queue.length > 0) {
    const l = queue.length;
    for (let i = 0; i < l; ++i) {
      let t = queue.shift();
      if (nodes[t.y][t.x].isExit === true) {
        let path = [];
        while (t.predecessor !== undefined) {
          path.unshift(`[${t.y - 1}, ${t.x - 1}]`);
          t = t.predecessor;
        }
        //console.log(path);
        return [visited, path];
      }
      visited.push(`[${t.y - 1}, ${t.x - 1}]`);
      if (!nodes[t.y][t.x + 1].isWallOrVisited) {
        queue.push(nodes[t.y][t.x + 1]);
        nodes[t.y][t.x + 1].predecessor = t;
        nodes[t.y][t.x + 1].isWallOrVisited = true;
      }
      if (!nodes[t.y - 1][t.x].isWallOrVisited) {
        queue.push(nodes[t.y - 1][t.x]);
        nodes[t.y - 1][t.x].predecessor = t;
        nodes[t.y - 1][t.x].isWallOrVisited = true;
      }
      if (!nodes[t.y][t.x - 1].isWallOrVisited) {
        queue.push(nodes[t.y][t.x - 1]);
        nodes[t.y][t.x - 1].predecessor = t;
        nodes[t.y][t.x - 1].isWallOrVisited = true;
      }
      if (!nodes[t.y + 1][t.x].isWallOrVisited) {
        queue.push(nodes[t.y + 1][t.x]);
        nodes[t.y + 1][t.x].predecessor = t;
        nodes[t.y + 1][t.x].isWallOrVisited = true;
      }
    }
  }
  return [visited, []];
}

export default function dijkstra(cells, rows, cols) {
  let nodes = [];
  let startX, startY;
  for (let i = 0; i < rows + 2; ++i) {
    let row = [];
    for (let j = 0; j < cols + 2; ++j) {
      row.push(new Node(i, j));
      if (i === 0 || i === rows + 1 || j === 0 || j === cols + 1) {
        row[j].isWallOrVisited = true;
      } else {
        switch (cells[i - 1][j - 1].type) {
          case "wall":
            row[j].isWallOrVisited = true;
            break;
          case "enter":
            row[j].isEnter = true;
            startX = j;
            startY = i;
            break;
          case "exit":
            row[j].isExit = true;
            break;
          default:
        }
      }
    }
    nodes.push(row);
  }
  console.log(nodes);
  return alg(nodes, startY, startX);
}
