import React, { Component } from "react";

import "./Visualizer.css";
import dfs from "./alghorithms/dfs";
import dijkstra from "./alghorithms/dijkstra";
import A_Star from "./alghorithms/a-star";

class Cell {
  type = "";
  constructor() {
    this.type = "default";
  }
}

let startButton = undefined;

const rows = 80;
const cols = 80;

let enterRow = 10;
let enterCol = 10;
let exitRow = 10;
let exitCol = 30;

const cells = [];
let isMousePressed = false;
let isWorking = false;
let isStoping = false;
let isClearNeeded = false;
let paintType = "wall";

function changePaintType(s) {
  paintType = s;
  document.getElementById("paint-enter").className = "paint-button";
  document.getElementById("paint-exit").className = "paint-button";
  document.getElementById("paint-wall").className = "paint-button";
  document.getElementById("paint-" + s).className = "paint-button-active";
}

function changeEnter(row, col) {
  if (cells[row][col].type !== "exit") {
    cells[enterRow][enterCol].type = "default";
    document.getElementById(`[${enterRow}, ${enterCol}]`).className =
      "cell default";
  } else {
    cells[enterRow][enterCol].type = "exit";
    document.getElementById(`[${enterRow}, ${enterCol}]`).className =
      "cell exit";
    exitRow = enterRow;
    exitCol = enterCol;
  }
  cells[row][col].type = "enter";
  document.getElementById(`[${row}, ${col}]`).className = "cell enter";
  enterRow = row;
  enterCol = col;
}

function changeExit(row, col) {
  if (cells[row][col].type !== "enter") {
    cells[exitRow][exitCol].type = "default";
    document.getElementById(`[${exitRow}, ${exitCol}]`).className =
      "cell default";
  } else {
    cells[exitRow][exitCol].type = "enter";
    document.getElementById(`[${exitRow}, ${exitCol}]`).className =
      "cell enter";
    enterRow = exitRow;
    enterCol = exitCol;
  }
  cells[row][col].type = "exit";
  document.getElementById(`[${row}, ${col}]`).className = "cell exit";
  exitRow = row;
  exitCol = col;
}

function clearGrid() {
  for (let i = 0; i < rows; ++i) {
    for (let j = 0; j < cols; ++j) {
      let t = document.getElementById(`[${i}, ${j}]`);
      if (t.className === "cell visited" || t.className === "cell path") {
        t.className = "cell default";
      }
    }
  }
  isClearNeeded = false;
}

function onMouseDown(row, col) {
  if (!isWorking) {
    if (isClearNeeded) {
      clearGrid();
    }
    if (paintType === "wall") {
      if (cells[row][col].type === "default") {
        cells[row][col].type = "wall";
      } else if (cells[row][col].type === "wall") {
        cells[row][col].type = "default";
      }
      document.getElementById(`[${row}, ${col}]`).className =
        "cell " + cells[row][col].type;
      isMousePressed = true;
    } else if (paintType === "enter") {
      changeEnter(row, col);
    } else if (paintType === "exit") {
      changeExit(row, col);
    }
  }
}

function onMouseUp() {
  isMousePressed = false;
}

function onMouseEnter(row, col) {
  if (isMousePressed === true && !isWorking && paintType === "wall") {
    if (cells[row][col].type === "default") {
      cells[row][col].type = "wall";
    } else if (cells[row][col].type === "wall") {
      cells[row][col].type = "default";
    }
    document.getElementById(`[${row}, ${col}]`).className =
      "cell " + cells[row][col].type;
  }
}

async function animateVisited(ids, alghorithm) {
  let time = 5;
  if (ids.length < 20) {
    time = 200;
  } else if (ids.length < 40) {
    time = 100;
  } else if (ids.length < 80) {
    time = 50;
  } else if (ids.length < 140) {
    time = 25;
  }
  if (time > 5 && alghorithm === "A*") {
    time /= 5;
  }

  for (let i = 1; i < ids.length; ++i) {
    if (isStoping) {
      break;
    }
    document.getElementById(`${ids[i]}`).className = "cell visited";
    await new Promise((res) => setTimeout(res, time));
  }
}

async function animatePath(ids) {
  let time = 20;
  if (ids.length === 0) {
    alert("Путь не существует");
  }
  if (ids.length > 1000) {
    time = 15;
  }
  if (ids.length > 2500) {
    time = 10;
  }
  for (let i = 0; i < ids.length - 1; ++i) {
    if (isStoping) {
      break;
    }
    document.getElementById(`${ids[i]}`).className = "cell path";
    await new Promise((res) => setTimeout(res, time));
  }
}

function start() {
  if (startButton === undefined) {
    startButton = document.getElementById("startButton");
  }
  if (!isWorking) {
    startButton.innerHTML = "Стоп";
    startButton.className = "stop-button";
    visualize();
  } else {
    isStoping = true;
    startButton.innerHTML = "Пуск";
    startButton.className = "start-button";
  }
}

async function visualize() {
  if (isClearNeeded) {
    clearGrid();
  }
  isClearNeeded = true;
  isWorking = true;
  let visited = [];
  let path = [];
  let alghorithm = document.getElementById("alghorithms").value;
  switch (alghorithm) {
    case "dijkstra":
      [visited, path] = await dijkstra(cells, rows, cols);
      break;
    case "dfs":
      [visited, path] = await dfs(cells, rows, cols);
      break;
    case "A*":
      [visited, path] = await A_Star(cells, rows, cols);
    default:
  }
  await animateVisited(visited, alghorithm);
  await animatePath(path);
  isWorking = false;
  isStoping = false;
}

export default class Visualizer extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    for (let row = 0; row < rows; ++row) {
      const currentRow = [];
      for (let col = 0; col < cols; ++col) {
        currentRow.push(new Cell());
      }
      cells.push(currentRow);
    }
    cells[enterRow][enterCol].type = "enter";
    cells[exitRow][exitCol].type = "exit";
    this.setState(cells);
  }

  render() {
    return (
      <>
        <div className="menu">
          <div className="menu-box">
            <div className="menu-column">
              <button
                className="paint-button"
                id="paint-enter"
                onClick={() => changePaintType("enter")}
              >
                <img className="img" src="assets/green.bmp" alt="enter"></img>
                <p className="text">Точка начала</p>
              </button>
              <br></br>
              <button
                className="paint-button"
                id="paint-exit"
                onClick={() => changePaintType("exit")}
              >
                <img className="img" src="assets/gold.bmp" alt="exit"></img>
                <p className="text">Точка конца</p>
              </button>
              <br></br>
              <button
                className="paint-button-active"
                id="paint-wall"
                onClick={() => changePaintType("wall")}
              >
                <img className="img" src="assets/gray.bmp" alt="wall"></img>
                <p className="text">Стена</p>
              </button>
            </div>
            <div className="menu-column">
              <label htmlFor="alghorithms">Выберите алгоритм: </label>
              <br></br>
              <select id="alghorithms" name="alghorithms">
                <option value="dijkstra">Алгоритм Дейкстры</option>
                <option value="A*">A-star</option>
                <option value="dfs">Поиск в глубину</option>
              </select>
              <br></br>
              <button
                id="startButton"
                className="start-button"
                onClick={() => start()}
              >
                Пуск
              </button>
            </div>
          </div>
        </div>
        <div className="grid">
          {cells.map((r, row) => {
            return (
              <div className="row" key={row.toString()}>
                {r.map((c, col) => (
                  <div
                    className={"cell " + cells[row][col].type}
                    id={`[${row}, ${col}]`}
                    onMouseDown={() => onMouseDown(row, col)}
                    onMouseUp={() => onMouseUp()}
                    onMouseEnter={() => onMouseEnter(row, col)}
                    key={`${row}, ${col}`}
                  ></div>
                ))}
              </div>
            );
          })}
        </div>
      </>
    );
  }
}
