class Node {
  isEnter = false;
  isExit = false;
  isWallOrVisited = false;
  x;
  y;
  distance = Infinity;
  predecessor = undefined;
  constructor(row, col) {
    this.x = col;
    this.y = row;
    this.isExit = false;
    this.isWallOrVisited = false;
  }
}

class QElement {
  constructor(element, priority) {
    this.element = element;
    this.priority = priority;
  }
}

class PriorityQueue {
  constructor() {
    this.items = [];
    this.length = 0;
  }

  isEmpty() {
    if (this.items.length > 0) {
      return false;
    } else {
      return true;
    }
  }

  put(element, priority) {
    let qElement = new QElement(element, priority);
    let contain = false;
    for (let i = 0; i < this.items.length; ++i) {
      if (qElement.priority <= this.items[i].priority) {
        this.items.splice(i, 0, qElement);
        contain = true;
        break;
      }
    }
    if (!contain) {
      this.items.push(qElement);
    }
    ++this.length;
  }

  shift() {
    if (!this.isEmpty()) {
      --this.length;
      return this.items.shift().element;
    }
  }
}

let exitRow = 0;
let exitCol = 0;

function distance(t) {
  return Math.abs(t.x - exitCol) + Math.abs(t.y - exitRow) + t.distance;
}

function alg(nodes, startRow, startCol) {
  let queue = new PriorityQueue();
  let visited = [];
  queue.put(nodes[startRow][startCol], distance(startRow, startCol));
  while (queue.length > 0) {
    let t = queue.shift();
    if (t.isExit === true) {
      let path = [];
      while (t.predecessor !== undefined) {
        path.unshift(`[${t.y - 1}, ${t.x - 1}]`);
        t = t.predecessor;
      }
      return [visited, path];
    }
    t.isWallOrVisited = true;
    visited.push(`[${t.y - 1}, ${t.x - 1}]`);
    if (!nodes[t.y][t.x + 1].isWallOrVisited) {
      nodes[t.y][t.x + 1].predecessor = t;
      nodes[t.y][t.x + 1].distance = t.distance + 1;
      queue.put(nodes[t.y][t.x + 1], distance(nodes[t.y][t.x + 1]));
    }
    if (!nodes[t.y - 1][t.x].isWallOrVisited) {
      nodes[t.y - 1][t.x].predecessor = t;
      nodes[t.y - 1][t.x].distance = t.distance + 1;
      queue.put(nodes[t.y - 1][t.x], distance(nodes[t.y - 1][t.x]));
    }
    if (!nodes[t.y][t.x - 1].isWallOrVisited) {
      nodes[t.y][t.x - 1].predecessor = t;
      nodes[t.y][t.x - 1].distance = t.distance + 1;
      queue.put(nodes[t.y][t.x - 1], distance(nodes[t.y][t.x - 1]));
    }
    if (!nodes[t.y + 1][t.x].isWallOrVisited) {
      nodes[t.y + 1][t.x].predecessor = t;
      nodes[t.y + 1][t.x].distance = t.distance + 1;
      queue.put(nodes[t.y + 1][t.x], distance(nodes[t.y + 1][t.x]));
    }
  }
  return [visited, []];
}

export default function A_Star(cells, rows, cols) {
  exitRow = 0;
  exitCol = 0;
  let nodes = [];
  let startRow, startCol;
  for (let i = 0; i < rows + 2; ++i) {
    let row = [];
    for (let j = 0; j < cols + 2; ++j) {
      row.push(new Node(i, j));
      if (i === 0 || i === rows + 1 || j === 0 || j === cols + 1) {
        row[j].isWallOrVisited = true;
      } else {
        switch (cells[i - 1][j - 1].type) {
          case "wall":
            row[j].isWallOrVisited = true;
            break;
          case "enter":
            row[j].isEnter = true;
            startRow = i;
            startCol = j;
            row[j].distance = 0;
            row[j].isWallOrVisited = false;
            break;
          case "exit":
            exitRow = i;
            exitCol = j;
            row[j].isExit = true;
            row[j].isWallOrVisited = false;
            break;
          default:
        }
      }
    }
    nodes.push(row);
  }
  return alg(nodes, startRow, startCol);
}
