class Node {
  isEnter = false;
  isExit = false;
  isWallOrVisited = false;
  x;
  y;
  predecessor = undefined;
  constructor(row, col) {
    this.x = col;
    this.y = row;
    this.isExit = false;
    this.isWallOrVisited = false;
  }
}

let flag = false;
let visited = [];
let path = [];

function alg(nodes, y, x) {
  if (nodes[y][x].isExit) {
    path.push(`[${y - 1}, ${x - 1}]`);
    flag = true;
    return;
  }
  nodes[y][x].isWallOrVisited = true;
  visited.push(`[${y - 1}, ${x - 1}]`);
  path.push(`[${y - 1}, ${x - 1}]`);
  if (!flag && !nodes[y][x + 1].isWallOrVisited) {
    alg(nodes, y, x + 1);
  }
  if (!flag && !nodes[y + 1][x].isWallOrVisited) {
    alg(nodes, y + 1, x);
  }
  if (!flag && !nodes[y][x - 1].isWallOrVisited) {
    alg(nodes, y, x - 1);
  }
  if (!flag && !nodes[y - 1][x].isWallOrVisited) {
    alg(nodes, y - 1, x);
  }
  if (!flag) {
    path.pop();
  }
  return;
}

export default function dfs(cells, rows, cols) {
  flag = false;
  visited = [];
  path = [];
  let nodes = [];
  let startRow, startCol;
  for (let i = 0; i < rows + 2; ++i) {
    let row = [];
    for (let j = 0; j < cols + 2; ++j) {
      row.push(new Node(i, j));
      if (i === 0 || i === rows + 1 || j === 0 || j === cols + 1) {
        row[j].isWallOrVisited = true;
      } else {
        switch (cells[i - 1][j - 1].type) {
          case "wall":
            row[j].isWallOrVisited = true;
            break;
          case "enter":
            row[j].isEnter = true;
            startRow = i;
            startCol = j;
            console.log(startCol, startRow);
            row[j].isWallOrVisited = false;
            break;
          case "exit":
            row[j].isExit = true;
            row[j].isWallOrVisited = false;
            break;
          default:
        }
      }
    }
    nodes.push(row);
  }
  console.log(nodes);
  alg(nodes, startRow, startCol);
  path.shift();
  return [visited, path];
}
